:root {
  --font-heading: "Courier Prime", monospace;
  --font-body: "Raleway", sans-serif;
  --font-sub: "Roboto Slab", serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark: hsl(40,100%,10%);
  --lt-olive: hsl(88,16%,52%);
  --dk-olive: hsl(82,37%,25%);
  --burlywood: hsl(24,60%,75%);
  --brown: hsl(37,96%,23%);
  --dim-gray: hsl(0,0%,32%);
}

h2 {
  color: var(--lt-olive);
}

.description {
  background-color: var(--dim-gray);
}
.description h2 {
  color: var(--white);
}
.description h2 span {
  animation: glow 1s ease-in-out infinite alternate;
}
@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 10px var(--white), 0 0 20px var(--white), 0 0 30px var(--dim-gray), 0 0 40px var(--dim-gray);
  }
  to {
    text-shadow: 0 0 20px var(--white), 0 0 30px var(--white), 0 0 40px var(--dim-gray), 0 0 50px var(--dim-gray);
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-heading);
  text-transform: uppercase;
  font-weight: 400;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  color: var(--dark);
  font-size: 5rem;
}

h2 {
  font-size: 2.375rem;
}

p {
  line-height: 1.4;
}

.description h2 {
  font-size: 3rem;
}

.subtitle {
  font-family: var(--font-sub);
  font-size: 2.5rem;
}

#passion p,
#creators p {
  font-size: 1.25rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

header {
  display: flex;
  margin: 0.9375rem auto;
  max-width: 43.75rem;
}

h2 {
  margin-bottom: 0.875rem;
}

div {
  max-width: 67.5rem;
}

section {
  padding: 3rem;
}

img {
  width: 100%;
  max-width: 64rem;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.description h2 {
  margin-bottom: 1.25rem;
  max-width: 46.25rem;
}
.description p {
  max-width: 56.25rem;
}

#benefits {
  display: flex;
  justify-content: center;
}

.benefits {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3.5rem;
}
.benefits p {
  text-align: justify;
}
.benefits__movement {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
.benefits__movement h2 {
  grid-row: 1;
}
.benefits__movement p {
  grid-row: 2;
}
.benefits__growth {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
.benefits__growth h2 {
  grid-row: 1;
}
.benefits__growth p {
  grid-row: 2;
}

#passion,
#creators {
  display: flex;
  justify-content: center;
  padding: 3rem;
}